export default {
  async asyncData({ $content, params }) {
    const icon = await $content('svg-icons', params.slug).fetch()
    const [prev, next] = await $content('svg-icons')
      .sortBy('createdAt', 'asc')
      .surround(params.slug)
      .fetch()
    return {
      icon,
      prev,
      next
    }
  }

}
